.missed-calls-container {
  width: 100%;
  overflow-x: scroll;
  .ReactTable {
    .rt-table {
      .rt-tbody {
        .rt-tr-group {
          .rt-tr {
            .rt-td:nth-child(1),
            .rt-td:nth-child(2),
            .rt-td:nth-child(3),
            .rt-td:nth-child(5),
            .rt-td:nth-child(10),
            .rt-td:nth-child(10),
            .rt-td:last-child {
              text-align: center;
            }
          }
        }
      }
    }
  }
}

.missed-call-logs {
  display: flex;
  flex-wrap: wrap;
  .custom-badge {
    margin: 2px;
  }
}
